
<template>
    <div>
    <div v-if="(typeof(SearchPosition) === 'undefined' || SearchPosition == 'left') && SearchFields" class="left-menu-tab" id="crmleftsearch">
        <leftsearch :header="HeaderText"
                    v-bind:fields="SearchFields"
                    v-on:SetLeftSearch="leftCommonSearch"
                    :modulename="ModuleName">
        </leftsearch>
    </div>
    <div id="crmrightsearch" :class="{'right-content': ((typeof(SearchPosition) === 'undefined' || SearchPosition == 'left') && SearchFields)}">
        <div class="col-md-12 p-0" v-if="HeaderButtons">
            <list-header-buttonNew  v-if="SubModuleCode=='CRM_LEADS'" v-bind:icons="HeaderButtons"  
           v-on:RefreshManageView="RefreshManageView"  v-on:ButtonClick="commonHeaderButtonClick" :modulename="ModuleName" :submodulecode="SubModuleCode"></list-header-buttonNew>
             <list-header-buttons v-else v-bind:icons="HeaderButtons" v-on:ButtonClick="commonHeaderButtonClick"></list-header-buttons>
           
        </div>
        <div class="py-2 d-inline-block">
            <template v-for="lgnd in LegendArray">
                <span :class="lgnd.class"></span>{{ lgnd.text }}
            </template>
            
        </div>
        <div class="listing">
            <div class="expand-list" v-if="(typeof(SearchPosition) === 'undefined' || SearchPosition == 'left')">
                <a href="javascript:;" @click="ExpandCollapseList" v-bind:class="{'expand-list-btn justify-content-center align-items-center': true, 'filter-selected':IsFilterApplied}">
                    <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                </a>
            </div>
            <div class="text-center text-danger" v-if="ErrorText">{{ErrorText}}</div>
            <DataTable v-if="ListData"
                        :Data="ListData"
                        :noRecord="noRecord"
                        :Headers="HeaderData"
                        :SortExp="SortExp"
                        :SortBy="SortBy"
                        :totalrecords="TotalRecordsForPaging"
                        :IsShowAction="IsShowAction"
                        v-on:sortdata="sortdata"
                        v-on:ButtonClick="actionButtonClick"
                        v-on:PagerMethod="pagerMethod"
                        :renderRowActionMethod="RenderRowActionMethod"
                        :identityColumn="IdentityColumn"
                        :subModuleCode="SubModuleCode"
                        :CurrentPageNumber="CurrentPageNumber"                      
                        >
                        <template v-slot:slotdata="{data}">
                            <slot name="slotdata" :data="{row:data.row,column:data.column}"></slot>
                        </template>
            </DataTable>
        </div>
    </div>
</div>
</template>
<script>
import DataTable from '../../components/Common/DataTable.vue';
import leftsearch from '../../components/Common/left-search.vue';
import ListHeaderButtons from '../../components/Common/list-header-buttons.vue'
import ListHeaderButtonsNew from '../../components/Common/list-header-buttonNew.vue'
export default {
    props:{
        ListData:  null, 
        HeaderText:{
            type: String,
            required: true
        },
        HeaderData: null,
        IsShowAction: {
            type: String,
            required: true
        },
        SearchPosition:{
            type: String,
            required: false
        },
        SearchFields: {
            type: Array,
            required: true
        },
        LegendArray: null,
        HeaderButtons: {
            type: Array,
            required: true
        },
        ModuleName: {
            type: String,
            required: true
        },
        SubModuleCode: {
            type: String,
            required: true
        },
        IdentityColumn: {
            type: String,
            required: true
        },
        RenderRowActionMethod: {
            type: Function,
            required: true
        },
        TotalRecords: {
            type: Number,
            required: true
        },
        CurrentPageNumber:{
            type:Number
        },
        IsFilterApplied:null,
        SortExp:null,
        SortBy:null,
    },
    components: {
        leftsearch,
        DataTable,
        "list-header-buttons": ListHeaderButtons,
        "list-header-buttonNew":ListHeaderButtonsNew
    },
    data() {
        return {
            ErrorText : "",
            TotalRecordsForPaging: 0,
            noRecord: false           
        }
    },
    watch:{
        TotalRecords: function(v){
            var ths = this;
            setTimeout(() => {
                ths.TotalRecordsForPaging = v;
            }, (500));
        }
    },
    updated: function(){
        if(this.ListData && this.ListData.length > 0){
            this.noRecord = false;
        }else{
            this.noRecord = true;
        }
        this.ErrorText = "";
        if(typeof(this.ListData) === 'undefined'){
            this.ErrorText = 'Data is not provided to the TgList component';
        } 
        else if(typeof(this.ListData) !== 'object' && typeof(this.ListData) !== 'array'){
            this.ErrorText = 'Data is not an object or array - TgList';
        }
        if(typeof(this.HeaderData) === 'undefined'){
            this.ErrorText = 'Headers are not provided to the TgList component';
        } 
        else if(typeof(this.HeaderData) !== 'object' && typeof(this.HeaderData) !== 'array'){
            this.ErrorText = 'Header Data is not an object or array - TgList';
        }
        if(typeof(this.IdentityColumn) === 'undefined'){
            this.ErrorText = 'Identity Column are not provided to the TgList component';
        }
        if(typeof(this.ModuleName) === 'undefined'){
            this.ErrorText = 'Module Name are not provided to the TgList component';
        }
        if(typeof(this.SubModuleCode) === 'undefined'){
            this.ErrorText = 'Sub Module Code are not provided to the TgList component';
        }
        if(typeof(this.RenderRowActionMethod) === 'undefined' &&  this.IsShowAction == 'true'){
            this.ErrorText = 'Render Row Action Method are not provided to the TgList component';
        }
    },
    methods: {
        ExpandCollapseList() {
            var element1 = document.getElementById("crmleftsearch");
            element1.classList.toggle("left-menu-tab-collapsed");
            var element2 = document.getElementById("crmrightsearch");
            element2.classList.toggle("right-content-expand");
        },
        RefreshManageView(){
            this.$emit('RefreshManageView');
        },
        commonHeaderButtonClick: function (event) {
            this.$emit('HeaderButtonClick', event);
        },
        leftCommonSearch: function (value) {
            this.$emit('LeftsearchButtonClick', value);
        },
        pagerMethod: function (value) {
            this.$emit('PagerButtonClick', value);
        },
        sortdata: function (s) {
            this.$emit('SortdataButtonClick', s);
        },
        actionButtonClick:function(callbackfunction,item,index,event){
            this.$emit('ActionButtonClick', callbackfunction, item, index, event);
        },
       
    
    }
}
</script>